@media only screen and (max-width: 700px) {
    .pop-up-div{
        height: 90%;
        width: 70%;
        display: flex;
        flex-direction: column;
        overflow-y: scroll;
    }
    .pop-up-container-right{
        overflow-y: visible;
        overflow-y: initial;
    }
    .pop-up-container-right{
        padding: 0 2rem 2rem 2rem;
    }

    .pop-up-container-left{
        padding: 1rem 1rem 0 1rem;
    }
