  justify-content: center;
  
}

.main-container{
  width: 90%;

}
.main-title{
font-family: 'Tangerine';
font-style: normal;
font-weight: bold;
font-size: 3rem;
text-align: center;
letter-spacing: 0.1em;
color: #1F638C;
}

.main-title-div{

    background-color: #F6F1F1;
    height: 4rem;
    display: grid;
    grid-template-columns: 15% 70% 15%;
