}

.windows-bottom-panes-rectangles-left {
  background: #BF3C1F;
  position: absolute;
  bottom: 19%;
  left: 19%;
  width: 24%;
  height: 75%; 
}

.windows-bottom-panes-rectangles-right {
  background: #BF3C1F;
